* {
  box-sizing: border-box;
}
.blank {
  height: 700px;
}
.region__select {
  text-align: center;
  margin-top: 30px;
}

.region__select button {
  width: 120px;
  padding: 0;
  margin: 35px 20px 10px 25px;
  font-weight: 1000;
  text-align: center;
  line-height: 50px;
  color: white;
  border-radius: 25px;
  border: solid #cc0000;
  transition: all 0.2s;
  background-color: #cc0000;
  box-shadow: 0px 0px 0px 0px #21825b;
}

.btnBlueGreen.btnBorder {
  box-shadow: 0px 0px 0px 0px #21825b;
}

.region__select button:hover {
  box-shadow: 0px 0px 0px 5px black;
}
body {
  background-color: rgb(230, 230, 230);
  justify-content: center;
}
.grid-container {
  max-width: 100%;
  display: grid;
  grid-template-columns: 253px 253px 253px 253px;
  grid-gap: 50px;
  text-align: center;
  justify-content: center;
}
@media only screen and (max-width: 768px) {
  .grid-container {
    max-width: 70%;
    display: grid;
    margin: 30px auto;
    grid-template-columns: auto;
    grid-gap: 50px;
    text-align: center;
    justify-content: center;
  }
}
.btn {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.btn button {
  border-style: none;
  padding: 5px 10px;
  margin-right: 10px;
  border-radius: 5px;
  background: #30a7d7;
  color: #fff;
  font-size: 1em;
}
