.search {
  margin-top: 50px;
  margin-bottom: 50px;
  justify-content: center;
  text-align: center;
}

.search input {
  background-color: #cc0000;
  text-align: center;
  justify-content: center;
  width: 300px;
  height: 60px;
  border-radius: 40px;
  border: 5px solid #cc0000;
  transition: all 0.2s;
  font-size: 20px;
  color: white;
  caret-color: rgba(255, 255, 255, 0.452);
}
.search input::placeholder {
  font-size: 20px;
  vertical-align: auto;
  color: rgba(255, 255, 255, 0.452);
  caret-color: transparent;
}
.search input:hover {
  box-shadow: 0px 0px 0px 5px black;
}

.search input:focus-visible {
  border: 0px;
  outline: 0px;
}
